<template>
  <div>
    <div class="search_container">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="销售组织" size="mini">
          <el-select clearable v-model="formInline.company_id" placeholder="请选择" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="客户名称" size="mini">
          <el-input v-model="formInline.customer_name"></el-input>
        </el-form-item>

        <el-form-item label="业务员" size="mini">
          <el-select clearable v-model="formInline.employee_name" filterable placeholder="">
            <el-option :label="item.employee_name" :value="item.employee_name" :key="item.employee_id" v-for="item in employeeList.options"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务员手机号码" label-width="130" size="mini">
          <el-input v-model="formInline.mobile"></el-input>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addFun" class="addBtn" size="mini">新增</el-button>
      <el-button plain @click="untieFun" size="mini">解绑</el-button>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" max-height="600" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="company_name" label="销售组织"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="employee_name" label="业务员名称"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="mobile" label="业务员手机号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="customer_name" label="客户名称"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="province_name" label="客户区域">
          <template slot-scope="scope">
            {{scope.row.province_name}} {{scope.row.city_name}} {{scope.row.area_name}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="customer_mobile" label="客户手机号"></el-table-column>

      </el-table>
    </div>
    <div class="page_container">
      <div class="pageNumber">每页显示</div>
      <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
      </el-pagination>
    </div>
    <el-dialog :close-on-click-modal="false" title="添加绑定关系" center :visible.sync="dialogFormVisible" width="1030px" @close="closeDialog('ruleForm')">
      <el-form :inline="true" :model="ruleForm" ref="ruleForm" class="dialogForm">
        <el-form-item label="采购组织" size="mini" prop="company_id" :rules="rules.company_id">
          <el-select clearable v-model="ruleForm.company_id" @change="companyChange(ruleForm.company_id,'ruleForm')" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id" :disabled="item.is_choose==0">
              <span :class="item.type==1 ?'optionStyle':''">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="部门" size="mini" prop="department_id" :rules="rules.department_id">
          <el-select clearable v-model="ruleForm.department_id" placeholder="请选择" @change="departmentChange(ruleForm.department_id,'ruleForm')" filterable>
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList.options"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务员" size="mini" prop="employee_id" :rules="rules.business_id">
          <el-select clearable v-model="ruleForm.employee_id" placeholder="">
            <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList.options"></el-option>
          </el-select>
        </el-form-item>
        <br>
        <el-form-item label="省" size="mini" prop="province_id" :rules="[{ required: true, message: '请选择所属省', trigger: 'change' }]">
          <el-select clearable v-model="ruleForm.province_id" placeholder="" @change="changeProvince" class="shortSearchInput">
            <el-option :label="item.area_name" :value="item.area_id" :key="item.area_id" v-for="item in province.options"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="市" size="mini">
          <el-select clearable v-model="ruleForm.city_id" @change="changeCity" class="shortSearchInput">
            <el-option :label="item.area_name" :value="item.area_id" :key="item.area_id" v-for="item in city.options"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="县" size="mini">
          <el-select clearable v-model="ruleForm.area_id" placeholder="" class="shortSearchInput">
            <el-option :label="item.area_name" :value="item.area_id" :key="item.area_id" v-for="item in area.options"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="客户名称" size="mini">
          <el-input v-model="ruleForm.customer_name"></el-input>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button plain @click="onUntieSearch" size="mini">查询</el-button>
        </el-form-item>
      </el-form>
      <div>
        <el-transfer v-model="untieValue" :props="{
                              key: 'customer_id',
                              label: 'customer_name'
                            }" :titles="['待绑定', '已绑定']" :data="untieData">
        </el-transfer>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogFormsubmit('ruleForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  getData,
  getCompanyListCommon,
  getDepartmentListCommon,
  getEmployeeListCommon
} from "common/utils/public_function/commonUtils";
export default {
  name: "customeCustomersaleman",
  data() {
    return {
      authority: {},

      formInline: {},
      formInlineParams: {},
      employeeList: { options: [] },
      companyList: [],
      departmentList: {
        options: []
      },
      province: { options: [] },
      city: { options: [] },
      area: { options: [] },
      TableData: {
        tableData: [],
        per_page: [30, 50, 100, 500],
        total: 10
      },
      per_page: [30, 50, 100, 500],
      pageSize: 30,
      multipleSelection: [],
      dialogFormVisible: false,
      ruleForm: { is_page: 1 },
      goPage: 1, //默认搜索页面

      untieData: [],
      untieValue: []
    };
  },
  methods: {
    companyChange(val, formName) {
      getDepartmentListCommon(this, this[formName], 1);
    },
    departmentChange(val, formName) {
      getEmployeeListCommon(this, this[formName], 1);
    },
    onSearch() {
      Object.assign(this.formInlineParams.params, this.formInline, {
        page: 1
      });
      getData(this.formInlineParams);
    },
    onReset() {
      this.$set(this, "formInline", {});
      this.$set(this, "formInlineParams", {
        params: {},
        TableData: this.TableData, //列表数据
        API: "getBindList", //接口名
        vm: this //传递的this
      });
      getData(this.formInlineParams);
    },
    addFun() {
      this.dialogFormVisible = true;
    },
    untieFun() {
      //解绑
      console.log(this.multipleSelection);
      let customer_id = [];
      for (let i = 0; i < this.multipleSelection.length; i++) {
        customer_id.push(this.multipleSelection[i].customer_id);
      }
      let vm = this;
      vm.$axios({
        method: "post",
        url: vm.$api.unbindEmployee,
        data: {
          customer_id: customer_id.toString()
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.$common.initData("getBindList", this.TableData, {
            // company_id: Number(localStorage.company_id)
          });
        }
      });
    },
    dialogFormsubmit(ruleForm) {
      this.$refs[ruleForm].validate(valid => {
        if (valid) {
          let vm = this;
          vm.$axios({
            method: "post",
            url: vm.$api.addBindEmployee,
            data: {
              employee_id: vm.ruleForm.employee_id,
              company_id: vm.ruleForm.company_id,
              department_id: vm.ruleForm.department_id,
              customer_id: vm.untieValue.toString()
            }
          }).then(res => {
            if (res.data.code == 200) {
              this.$message({
                message: res.data.data,
                type: "success"
              });
              this.dialogFormVisible = false;
              this.$common.initData("getBindList", this.TableData, {
                company_id: Number(localStorage.company_id)
              });
            }
          });
        }
      });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      Object.assign(this.formInlineParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.formInlineParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.formInlineParams.params, {
        page: val
      });
      getData(this.formInlineParams);
    },
    changeProvince() {
      this.ruleForm.city_id = "";
      this.$common.options("getAreaListOption", this.city, {
        level: 3,
        p_id: this.ruleForm.province_id
      });
    },
    changeCity() {
      this.$common.options("getAreaListOption", this.area, {
        level: 4,
        p_id: this.ruleForm.city_id
      });
    },
    changeCompanyDialog() {
      this.$common.options("getDepartmentListOption", this.department, {
        company_id: this.ruleForm.company_id
      });
    },
    onUntieSearch() {
      let vm = this;
      vm.$axios({
        method: "get",
        url: vm.$api.getNoUntieCustomerList,
        params: vm.ruleForm
      }).then(res => {
        if (res.data.code == 200) {
          console.log(res.data.data);
          this.untieData = res.data.data;
        }
      });
    },
    closeDialog(ruleForm) {
      this.ruleForm = {};
      this.$refs[ruleForm].resetFields();
    }
  },
  mounted() {
    this.$set(this, "formInlineParams", {
      params: {}, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getBindList", //接口名
      vm: this //传递的this
    });
    getData(this.formInlineParams);
    this.$common.options("getAreaListOption", this.province, { level: 2 });
    getCompanyListCommon(this); //获取组织
    // this.$common.options("getEmployeeListOption", this.employeeList); //业务员
  }
};
</script>

<style lang="less" scoped>
</style>
